<!---->
<style lang='scss' rel='stylesheet/scss'>
    #client-bootad-contaienr {
        background: #fff;
        padding: 15px;
        border-radius: 5px;

        /**/
        @at-root {
            .uploadimg {
                > div {
                    display: inline-block;
                }

                .img-container {
                    width: 200px;
                    height: 264px;
                    border: solid 1px #ccd0d4;
                    background: #f8f8f8;

                    &:before {
                        content: "1280 x 1706";
                        font-size: 20px;
                        height: 264px;
                        line-height: 264px;
                        display: block;
                        text-align: center;
                        color: #666;
                    }

                    img {
                        height: 100%;
                        z-index: 9;
                        position: absolute;
                        width: 198px;
                        top: 0;
                        left: 1px;
                    }
                }

                .img-desc {
                    vertical-align: middle;
                    color: #bbb;
                    margin-left: 12px;
                    span {
                        font-size: 13px;
                        line-height: 1.4em;
                        display: block;
                    }

                    a {
                        font-size: 13px;
                        color: #337ab7;
                        text-decoration: none;
                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
            }
        }
    }
</style>

<template>
    <article id="client-bootad-contaienr">
        <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px">
            <el-form-item label="" v-loading="uploadingImg"
                          element-loading-text="">
                <section class="uploadimg">
                    <div class="img-container">
                        <img :src="imgData | fillImgPath" v-show="imgData">
                    </div>
                    <div class="img-desc">
                        <span>
                             1280x1706<br>
                        </span>
                        <a href="javascript:;" @click="isShowImgSample = true"></a>
                        <ImagEcropperInput :aspectRatio="1280/1706" :confirmFn="cropperFn"></ImagEcropperInput>
                    </div>
                </section>
            </el-form-item>
            <el-form-item label="" prop="logo_app_boot">
                <el-input v-model="form.logo_app_boot" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="logo_app_priority">
                <el-radio-group v-model="form.logo_app_priority">
                    <el-radio v-model="form.logo_app_priority" :label="0"></el-radio>
                    <el-radio v-model="form.logo_app_priority" :label="1"></el-radio>
                    <el-radio v-model="form.logo_app_priority" :label="2"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>

        <!---->
        <el-dialog title="" v-model="isShowImgSample" size="small" top="15px">
            <img src="" style="width: 100%">
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="isShowImgSample = false"> </el-button>
            </span>
        </el-dialog>
    </article>
</template>

<script>
    import ImagEcropperInput from '../component/upload/ImagEcropperInput.vue'
    import mobileService from '../../services/app/mobileService.js'
    import authUtils from '../../utils/authUtils'
    import commonService from '../../services/commonService.js'
    export default{
        data() {
            return {
                imgData: '',
                uploadingImg: false,
                isShowImgSample: false, // 
                form: {
                    logo_app_boot: '', // 
                    logo_app_priority: 1, // 
                },
                rules: {
                    logo_app_priority: [
                        {type: 'number', message: '', trigger: 'blur'}
                    ],
                }
            }
        },
        activated () {
            this.user = authUtils.getUserInfo()
            mobileService.getBoot({gov_id: this.user.gov_id}).then((ret) => {
                console.log(ret)
                this.imgData = ret.banner
                // this.banner = ret.banner
                this.form = {
                    logo_app_boot: ret.logo_app_boot,
                    logo_app_priority: ret.status
                }
            }).then(() => {
                xmview.setContentLoading(false)
            })
        },
        methods: {
            cropperFn(data) {
                this.uploadingImg = true
                // 
                commonService.commonUploadImageBase({gov_id: this.user.gov_id, image: data,extpath:'banner'}).then(ret => {
                    this.imgData = ret.url
                }).then(() => {
                    // xmview.showTip('success', '')
                    this.uploadingImg = false
                }).catch((ret) => {
                    xmview.showTip('error', ret.message)
                    this.uploadingImg = false
                })
            },
            submitForm() {
                this.$refs['ruleForm'].validate((valid) => {
                    if (valid) {
                        mobileService.updateBoot({
                            gov_id: this.user.gov_id,
                            banner: this.imgData,
                            logo_app_boot: this.form.logo_app_boot,
                            status: this.form.logo_app_priority
                        }).then((ret) => {
                            xmview.showTip('success', '')
                        }).catch((ret) => {
                            xmview.showTip('error', ret.message)
                        })
                    } else {
                        return false
                    }
                })
            }
        },
        components: {
            ImagEcropperInput
        }
    }
</script>



